$dayCellSize: 100px;
$dayCellGap: 1px;
$calendaryHorizantalPadding: 10px;
$calendarWidth: 
  7 * $dayCellSize  + 6 * $dayCellGap + 2 * $calendaryHorizantalPadding;

$mainFont: arial, "Helvetica Neue", helvetica, sans-serif;
$fontSize: 30px;

$days:
  "mon" 1,
  "tue" 2,
  "wed" 3,
  "thu" 4,
  "fri" 5,
  "sat" 6,
  "sun" 7;
